import React from 'react'
import { connect } from 'react-redux'
import { addTodo } from '../actions'

const AddingTodo = ({ dispatch }) => {
	let input;
	let submitHandler = (e) => {
		e.preventDefault();
		if (!input.value.trim()) {
			return
		}
		dispatch(addTodo(input.value));
		input.value = ''
	};
	
	return (
		<div>
			<form className="form-inline"
				onSubmit={submitHandler}
			>
				<div className="form-group">
					<input className="form-control mr-3 add-input"
						ref={node => input = node} />
				</div>
				<div className="form-group">
					<button className="btn btn-primary" type="submit">
						Add Todo
					</button>
				</div>
				
			</form>
		</div>
	)
};

export default connect()(AddingTodo)


